@import url(https://fonts.googleapis.com/css2?family=Akaya+Kanadaka&family=Ubuntu&display=swap);
body{background-color:#000;height:100%;width:100%;margin:0;padding:0 10rem;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body::-webkit-scrollbar{width:0.7rem}body::-webkit-scrollbar-track{background:black}body::-webkit-scrollbar-thumb{height:20px;background:goldenrod}@media screen and (max-width: 798px){body{padding:0 5rem;height:auto}}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}html{font-size:62.5%;margin:0;padding:0;width:100%;height:100%}@media (max-width: 768px){html{font-size:49%;height:auto}}@media (max-width: 468px){html{font-size:39%}}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.Navbar{height:10rem;width:100%;padding:5rem 10rem;display:flex;z-index:30;justify-content:space-between;align-items:center;font-size:2rem}.Navbar .Hlink{color:#f5f5f5;text-decoration:none}.Navbar .links{width:15%;display:flex;justify-content:center;align-items:center}.Navbar .links a{margin:0 2rem;font-size:2rem;color:#f5f5f5;text-decoration:none}@media screen and (max-width: 448px){.Navbar .links a{font-size:2.5rem}}@media screen and (max-width: 448px){.Navbar .links{width:30%}}@media (max-width: 768px){.Navbar{justify-content:space-between;padding:5rem 5rem}.Navbar a{font-size:2rem}}@media screen and (max-width: 440px){.Navbar{padding:2rem 2rem}.Navbar a{font-size:3rem}}.sticky{position:fixed;width:80%;-webkit-backdrop-filter:blur(2px);backdrop-filter:blur(2px)}@media (max-width: 768px){.sticky{width:75%;justify-content:space-between;padding:5rem 5rem}}

.social{width:100%;font-size:1rem;left:0;cursor:pointer;justify-content:center;align-items:center;display:flex;transition:2s;letter-spacing:.3rem}.Home{width:100%;height:100%;padding:2rem 0;margin-bottom:5rem;background-color:#141414;border-radius:5rem;border:.2rem solid goldenrod;box-shadow:0 4px 8px 0 gold, 0 6px 20px 0 goldenrod}@media screen and (max-width: 768px){.Home{height:auto;padding-bottom:5rem}}.Home .container{padding:5rem;display:flex;align-items:center}.Home .container .left{display:flex;justify-content:flex-start;align-items:center;flex-wrap:wrap}.Home .container .left h1{text-align:center;padding:2rem 5rem;font-size:3rem;text-align:center;color:#f5f5f5}.Home .container .left h1 .hero_name{font-family:'Ubuntu', sans-serif;font-size:6rem;font-weight:bolder;width:100%;left:0;z-index:2;text-align:center;background:linear-gradient(135deg, #f2d020 58%, #e8990f 76%, #e7ae0d 82%, #e29611 89%, #db2916 99%);-webkit-background-clip:text;-webkit-text-fill-color:transparent}@media screen and (max-width: 448px){.Home .container .left h1 .hero_name{font-size:8rem}}@media screen and (max-width: 448px){.Home .container .left h1{font-size:5rem}}.Home .container .left .underline{height:5px;width:65%;position:relative;left:15%;background-color:whitesmoke;margin-top:2rem}.Home .container .left p{color:gold;position:relative;bottom:0rem;padding:2rem 6rem;font-size:2rem;-webkit-text-decoration:double;text-decoration:double}@media screen and (max-width: 448px){.Home .container .left p{font-size:3rem}}.Home .container .right{display:flex;height:100%;justify-content:flex-end;align-items:center}@media screen and (width: 448px){.Home .container .right{height:auto}}.Home .container .right .hero_img{display:flex;justify-content:center;align-items:center;padding:2rem 3rem;flex-direction:column;position:relative;width:50rem}@media screen and (width: 768px){.Home .container .right .hero_img{width:40rem;height:40rem}}@media screen and (max-width: 440px){.Home .container .right .hero_img{width:45rem;height:45rem}}.Home .container .right .hero_img img{width:40rem;height:40rem;-webkit-filter:grayscale(60%) contrast(96%);filter:grayscale(60%) contrast(96%);border-radius:2rem;border:.5rem solid goldenrod}@media screen and (max-width: 768px){.Home .container .right .hero_img img{width:40rem;height:40rem}}@media screen and (max-width: 440px){.Home .container .right .hero_img img{width:45rem;height:45rem}}@media screen and (max-width: 678px){.Home .container{flex-direction:column;width:auto;border-radius:2rem}}.Home .buttons{position:relative;display:flex;justify-content:center}.Home .buttons .btn{display:inline-block;cursor:pointer;padding:1rem 3rem;margin:1rem;border:.2rem solid goldenrod;box-shadow:.2rem .2rem .2rem goldenrod;background:linear-gradient(135deg, #f2d020 20%, #e8990f 43%, #e7730d 75%, #db1673 93%, #e21111 100%);border-radius:1.5rem;text-align:center;color:#f5f5f5;font-size:1.5rem;transition:1s ease-out}.Home .buttons .btn:hover{background-color:transparent;background:whitesmoke;color:#000;border:.3rem solid gold}.Home .buttons .btn:hover{background-color:whitesmoke}@media screen and (max-width: 440px){.Home .buttons{bottom:0;justify-content:space-evenly;font-size:3.5rem}}

